import React from 'react';
// import { withRouter } from 'react-router-dom';
import { NavBar, Icon, TabBar, Button } from 'antd-mobile';
const Shop = (props) => {
    return (
        <div className='doc'>
            <Icon type='left' size='lg' onClick={() => props.history.goBack()} />
            <div>
                <img src={props.location.src} />
            </div>

            <div className="btnav">
                <div className='btns'>
                    <button className='btn1'>
                        收藏
                    </button>
                    <button className='btn2'>
                        客服
                        </button>
                    <button className='btn3'>
                        店铺
                        </button>
                    <button className='btn4'>
                        加入购物车
                        </button>
                    <button className='btn5'>
                        立即购买
                        </button>
                </div>
            </div>

        </div>
    )

}


export default Shop;