import { Carousel, WingBlank } from 'antd-mobile';
import React, { Component } from 'react';

class Lunbo extends React.Component {
  state = {
    data: ['1', '2', '3'],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['https://gw.alicdn.com/imgextra/i3/189/O1CN0101BLCD1DGbUJJRfeY_!!189-0-lubanu.jpg', 
        'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i2/6000000006874/O1CN01qev2pI20eLBaH5e3g_!!6000000006874-0-octopus.jpg', 
        'https://gw.alicdn.com/imgextra/i2/160/O1CN01jZXP2p1D3K06qncRt_!!160-0-lubanu.jpg'],
      });
    }, 100);
  }
  render() {
    return (
        <Carousel
          autoplay
          infinite
          beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={val}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
    );
  }
}
export default Lunbo;